@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
body, html {
  margin: 0;
  font-family: 'Mulish', sans-serif;
  scroll-behavior: smooth;
}

.App {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.Header {
  padding: 30px;
  text-align: center;
}

.welcomeHeader {
  color: #000000;
}

.GetStarted {
  padding: 30px;
  text-align: center;
}

.primaryButton {
  background-color: #e74c3c;
  color: #fff;
  min-width: 150px;
  height: 44px;
  border: 0;
  border-radius: 4px;
  cursor: pointer;
  box-shadow: 0 1px 3px rgb(0 0 0 / 12%), 0 1px 2px rgb(0 0 0 / 24%);
  transition: box-shadow .3s cubic-bezier(.25,.8,.25,1);
  margin: .67em 0;
  outline: none;
}

.primaryButton:hover {
  box-shadow: 0 14px 28px rgb(0 0 0 / 25%), 0 10px 10px rgb(0 0 0 / 22%);
}

.secondaryButton {
  background-color: #fff;
  color: #e74c3c;
  min-width: 100px;
  height: 44px;
  border: solid 1px  #e74c3c;
  border-radius: 4px;
  cursor: pointer;
  margin: .67em 0;
  outline: none;
  margin-right: 10px;
}

h1 {
  margin: 0px;
}

canvas {
  margin: auto;
}

.startContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #e74c3c;
}

.startContent {
  background-color: #fff;
  padding: 50px;
  border-radius: 4px;
  text-align: center;
  margin: 20px;
}

.buttonLeft {
  margin-right: 1em;
  margin-top: 2em;
}

.buttonRight {
  margin-left: 1em;
  margin-top: 2em;
}

.linkButton {
  background-color: #fff;
  border: none;
  outline: none;
  -webkit-text-decoration: dotted underline;
          text-decoration: dotted underline;
  cursor: pointer;
}

.backdrop {
  background-color: #e74c3c;
  z-index: 9999999;
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.backdrop div {
  padding: 20px;
}

/* MEDIA QUERIES */

@media only screen and (max-width: 466px) {
  .buttonLeft {
    margin-right: 1em;
    margin-left: 1em;
  }
  .buttonRight {
    margin-left: 1em;
    margin-right: 1em;
  }
}
html, body, #root {
    height: 100%;
    background-color: #e74c3c;
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    height: 100%;
    align-content: center;
}
  
.content {
    background-color: #fff;
    padding: 50px;
    border-radius: 4px;
    text-align: center;
    margin: 20px;
}

h2 {
    margin: 0;
    padding-bottom: 20px;
}
  
  h4 {
    font-weight: 400;
}
  
p {
    margin: 0;
    padding-top: 20px;
}
  
.manualForm {
    text-align: left;
}
  
.manualForm label, .manualForm input {
    display: block;
}
  
.manualForm input, textarea {
    margin-bottom: 1rem;
}
  
.manualForm input {
    height: 44px;
    width: 100%;
    border-radius: 4px;
    border: solid 1px #e0e0e0;
    -webkit-appearance: none;
    text-align: center;
}
  
.manualForm textarea {
    border-radius: 4px;
    border: solid 1px #e0e0e0;
    -webkit-appearance: none;
    width: 100%;
}
  
.manualForm input:focus, textarea:focus {
    outline: none;
    border: solid 1px #e74c3c;
    box-shadow: 10px 10px 20px -10px rgba(231, 76, 60, 0.2);
    -webkit-appearance: none;
}
  
th {
    border-bottom: solid 1px black;
}
  
th, td {
    padding: 10px;
    white-space: nowrap;
}

.overflow {
      overflow: auto;
}

.fullWidth {
    width: 100%;
}
  
table {
    background-color: #fff;
    padding: 50px;
    border-radius: 4px;
    text-align: center;
    margin: 20px;
}
  
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    -webkit-appearance: none; 
    margin: 0; 
}

.spinner {
    border: 2px solid #FAFAFA;
    border-top: 2px solid rgba(231, 76, 60, 0.2); 
    border-radius: 50%;
    width: 20px;
    height: 20px;
    animation: spin 1s linear infinite;
    display: inline-block;
    position: relative;
    top: 2px;
    cursor: pointer;
}

.submitted {
    box-shadow: none;
    transition: none;
    cursor: default;
}

.submitted:hover {
    box-shadow: none;
    transition: none;
}
  
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

/* MEDIA QUERIES */

@media only screen and (max-height: 730px) {
    .container {
      align-content: baseline;
    }
}
